    import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
	export default {
		data() {
			return {
				goods_content:[],
				pg_id:0,
				warning:''
			};
		},
        computed: mapState(['hasLogin','userInfo']),
		onLoad(e) {
			this.pg_id=e.id;
			this.loadData();
		},

		methods: {   
            async loadData() {  
				let _this = this;
				_this.$Request.post('pointprod/pinfo',{id:_this.pg_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.goods_content=res.datas.goods_content;
						_this.pg_id=res.datas.goods_content.pgoods_id;
						_this.warning = res.datas.goods_content.warning;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				});
				
				console.log('===userInfo==='+_this.hasLogin);
				console.log(_this.userInfo);
				console.log(_this.userInfo.token);
               
            },
			
			//确认支付
			confirm: async function() {
				let _this = this;
				if (!_this.hasLogin) {  
					uni.showToast({
						icon: 'none',
						title:'请先登录',
						duration:2000
					});
				 }else{
								
				
				}
				uni.showModal({
					title: '提示',
					content: '确认要兑换该商品吗？',
					success: function (res) {
						if (res.confirm) {
							 uni.showLoading({title: '正在处理中...'});
							let postData={pgid:_this.pg_id};
							_this.$Request.post('member_pointorder/pointbuy',postData).then(res => {
								if (res.code == 200) {
									console.log(res);
									uni.hideLoading();
									uni.showToast({
										icon: 'none',
										title:'兑换成功',
										duration:2000,
										complete:function(e){
											uni.navigateTo({
												url:"pointorder"
											})
										}
									});
									
								}else{
									uni.hideLoading();
									uni.showToast({
										icon: 'none',
										title:res.datas.error,
										duration:2000
									});
									
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
			
		}
	}
